import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Cell, List, Group, Div, Button} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Mention from '@vkontakte/icons/dist/24/mention';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/settings';
import Icon24LinkCircle from '@vkontakte/icons/dist/24/link_circle';
import Icon24Qr from '@vkontakte/icons/dist/24/qr';
import connect from '@vkontakte/vk-connect';

const osname = platform();

const Sett = ({ dark, share, light, go }) => (
	<Panel id="sett">
		<PanelHeader
			
		>
			Настройки		
		</PanelHeader>
        <Group title="Оформление">
		<List>
							<Cell onClick={dark}>
								Темная
							</Cell>	
                            <Cell onClick={light}>
								Стандартная
							</Cell>	
						</List>
                        </Group>


	</Panel>
);
Sett.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Sett;
