import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, Placeholder, HeaderButton, platform, Group, IOS, Cell, List, Div} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon56LockOutline from '@vkontakte/icons/dist/56/lock_outline';

const osname = platform();


const Top = props => (
	<Panel id="top">
		<PanelHeader
 
		>
			Топ	
		</PanelHeader>
      <Placeholder
            icon={<Icon56LockOutline />}
            stretched
          >
         Нам очень трудно об этом говорить, но <br /> раздел в данный момент <br /> недоступен. 
          </Placeholder>
	</Panel>
);

Top.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Top;
