import React from 'react';
import connect from '@vkontakte/vk-connect';
import { View, Alert, Tabbar, Epic, TabbarItem, ScreenSpinner, Spinner} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import Icon24Add from '@vkontakte/icons/dist/24/add';
import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24Poll from '@vkontakte/icons/dist/24/poll';
import Icon24Services from '@vkontakte/icons/dist/24/services';
import Icon24Settings from '@vkontakte/icons/dist/24/settings';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';

import Home from './panels/Home';
import Persik from './panels/Persik';
import House from './panels/House';
import Pet from './panels/Pet';
import Dop from './panels/Dop';
import Top from './panels/Top';
import Reit from './panels/Reit';
import Sett from './panels/Sett';
import ChangeNickname from './panels/ChangeNickname';
import Transfer from './panels/Transfer';
import Katalog from './panels/Katalog';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
            fetchedUser: null,
            userAcc: null,
			activeTab6: 'all',
            clicks: 0,
            reputation: 0,
            priv: null,

            activeStory: 'home', 
            name: false,
            popout: null,
            calcRep: 0,
            nick: null,
            toid: 0,
            transferCoins: 0,
            usersTop: null
		};
        this.closePopout = this.closePopout.bind(this);
        this.handleReputationChange = this.handleReputationChange.bind(this);
        this.handleReputationSubmit = this.handleReputationSubmit.bind(this);
        this.handleNickChange = this.handleNickChange.bind(this);
        this.handleNickSubmit = this.handleNickSubmit.bind(this);
         this.onStoryChange = this.onStoryChange.bind(this);
        this.handleTransferChange = this.handleTransferChange.bind(this);
        this.handleTransferSubmit = this.handleTransferSubmit.bind(this);
    }
    
    

	openPopout = () => {
		this.setState({ popout:
		  <Alert
			actions={[{
			  title: 'Ок',
			  autoclose: true,
			  style: 'destructive'
			}]}
			onClose={this.closePopout}
		  >
			<h2>Ошибка!</h2>
			<p>Недостаточно денег! Один рейтинг стоит 50000 кликов.</p>
		  </Alert>
		});
	  }
	  closePopout = () => {
		this.setState({ popout: null });
	  }

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
                    this.setState({ fetchedUser: e.detail.data });
                    //console.log(e.detail.data)
                    this.createUser(e.detail.data);
                    this.getUsers();
					break;
					case 'VKWebAppShowWallPostBoxResult':

  					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	}

    onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story })
    this.setState({ popout: <ScreenSpinner /> });
    setTimeout(() => { this.setState({ popout: null }) }, 200);
    }


    createUser = (data) => {
        fetch("https://vitekbot.online/api.php", {
            method: 'post',
            headers: {
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            },
            body: `method=user.create&user_vk=${data.id}&nickname=Игрок ${data.id}&first_name=${data.first_name}&last_name=${data.last_name}&clicks=0&reputation=0`
        })
            .then((response) => response.json())
            .then((response) => {
                //console.log(response)
                this.setState({ userAcc: response })
                this.setState({ clicks: response.clicks })
                this.setState({ reputation: response.reputation })
                //console.log(this.state.userAcc)
            })
            .catch((error) => {
                console.log(error)
            })
    }

    updateUserClicks = (id, click) => {
        fetch("https://vitekbot.online/api.php", {
            method: 'post',
            headers: {
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            },
            body: `method=user.update.click&user_vk=${id}&clicks_new=${click}`
        })
            .then((response) => response.json())
            .then((response) => {
                //console.log(response)
                this.setState({ clicks: Number(response.clicks) })
                this.setState({ reputation: Number(response.reputation) })
            })
            .catch((error) => {
                console.error(error)
            })
    }
    share = () => { 
      connect.send("VKWebAppJoinGroup", {"group_id": 1});
    }

    updateUserReputation = (id, reputation, click) => {
        fetch("https://vitekbot.online/api.php", {
            method: 'post',
            headers: {
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            },
            body: `method=user.update.reputation&user_vk=${id}&reputation_new=${reputation}&clicks_new=${click}`
        })
            .then((response) => response.json())
            .then((response) => {
               //console.log(response)
                this.setState({ clicks: Number(response.clicks) })
                this.setState({ reputation: Number(response.reputation) })
            })
            .catch((error) => {
                console.error(error)
            })
    }
    

    updateUserNickname = (id, nickname) => {
        fetch("https://vitekbot.online/api.php", {
            method: 'post',
            headers: {
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            },
            body: `method=user.update.nickname&user_vk=${id}&nickname=${nickname}`
        })
            .then((response) => response.json())
            .then((response) => {
                //console.log(response)
                this.setState({ userAcc: response })
                this.setState({ clicks: response.clicks })
                this.setState({ reputation: response.reputation })
            })
            .catch((error) => {
                console.error(error)
            })
    }

    sendTransfer = (id, toid, count) => {
        fetch("https://vitekbot.online/api.php", {
            method: 'post',
            headers: {
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            },
            body: `method=user.send.transfer&user_vk=${id}&toid=${toid}&count=${count}`
        })
            .then((response) => response.json())
            .then((response) => {
                //console.log(response)
                this.setState({ userAcc: response })
                this.setState({ clicks: response.clicks })
                this.setState({ reputation: response.reputation })
            })
            .catch((error) => {
                console.error(error)
            })
    }

    getUsers = () => {
        fetch("https://vitekbot.online/api.php", {
            method: 'post',
            headers: {
                "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
            },
            body: `method=get.users.top`
        })
            .then((response) => response.json())
            .then((response) => {
                this.setState({ usersTop: response })
                console.log(this.state.usersTop)
            })
            .catch((error) => {
                console.error(error)
            })
    }

    handleReputationChange(event) {
        const price = event.target.value * 50000;
        this.setState({ calcRep: price });
    }

    handleReputationSubmit(event) {
        if (this.state.clicks < 50000) { this.openPopout() }
        else {
            const rep = Number(this.state.calcRep) / 50000
            let clicks = Number(this.state.clicks) - Number(this.state.calcRep)
            let reputation = Number(this.state.reputation) + rep
            //this.setState({ rating: reputation, clicks: clicks})
            this.updateUserReputation(this.state.fetchedUser.id, reputation, clicks)
        }
        event.preventDefault();
    }

    handleNickChange(event) {
        console.log(event.target)
        this.setState({ nick: event.target.value})
    }

    handleNickSubmit(event) {
        if (this.state.nick === null) return false
        else this.updateUserNickname(this.state.fetchedUser.id, this.state.nick)
        event.preventDefault();
    }

    handleTransferChange(event) {
        console.log(event.target)
        const target = event.target;
        const name = target.name;
        const value = target.value;
        this.setState({ [name]: value })
    }

    handleTransferSubmit(event) {
        if (this.state.toid === 0 && this.state.transferCoins === 0) return false
        else this.sendTransfer(this.state.fetchedUser.id, this.state.toId, this.state.transferCoins)
        event.preventDefault();
    }


	add1 = () => {
        let clicks = Number(this.state.clicks) + 1;
        //this.setState({ clicks: clicks })
        this.updateUserClicks(this.state.fetchedUser.id, clicks)
    }
    


    dark = () => { 
        document.body.setAttribute( 'scheme','client_dark' ); 
    }
    
    light = () => { 
        document.body.setAttribute( 'scheme','client_light' ); 
    }


	render() {
		return (
			

<Epic activeStory={this.state.activeStory} tabbar={
            <Tabbar>
              <TabbarItem
                onClick={this.onStoryChange}
                selected={this.state.activeStory === 'top'}
                data-story="top"
            ><Icon24Poll/></TabbarItem>
            <TabbarItem
                onClick={this.onStoryChange}
                selected={this.state.activeStory === 'home'}
                data-story="home"
            ><Icon24Home/></TabbarItem>
            <TabbarItem
                onClick={this.onStoryChange}
                selected={this.state.activeStory === 'Transfer'}
                data-story="Transfer"
            ><Icon24Services/></TabbarItem>
            <TabbarItem
                onClick={this.onStoryChange}
                selected={this.state.activeStory === 'reit'}
                data-story="reit"
            ><Icon24Coins/></TabbarItem>
            <TabbarItem
                onClick={this.onStoryChange}
                selected={this.state.activeStory === 'sett'}
                data-story="sett"
            ><Icon24Settings/></TabbarItem>
            </Tabbar>
        }>

            <View  clicks={this.state.clicks}  reputation={this.state.reputation} add1={this.add1} userAcc={this.state.userAcc} popout={this.state.popout} id="home" activePanel="home" >
            <Home   clicks={this.state.clicks}  reputation={this.state.reputation} add1={this.add1} userAcc={this.state.userAcc} id="home" go={this.go} />
            </View>
            <View popout={this.state.popout} topUsers={this.state.usersTop} id="top" activePanel="top" >
            <Top topUsers={this.state.usersTop} id="top" go={this.go} />
            </View>
            <View id="sett" share={this.share} popout={this.state.popout} dark={this.dark} light={this.light} activePanel="sett" >
            <Sett id="sett" share={this.share} dark={this.dark} light={this.light} go={this.go} />
            </View>
            <View id="Transfer"popout={this.state.popout}  handleTransferChange={this.handleTransferChange} handleTransferSubmit={this.handleTransferSubmit} userAcc={this.state.userAcc} activePanel="Transfer" >
            <Transfer id="Transfer" popout={this.state.popout} handleTransferChange={this.handleTransferChange} handleTransferSubmit={this.handleTransferSubmit} userAcc={this.state.userAcc} go={this.go} />
            </View>
              <View id="reit" clicks={this.state.clicks} reputation={this.state.reputation} handleReputationChange={this.handleReputationChange} handleReputationSubmit={this.handleReputationSubmit} calcRep={this.state.calcRep} popout={this.state.popout} handleTransferChange={this.handleTransferChange} handleTransferSubmit={this.handleTransferSubmit} userAcc={this.state.userAcc} activePanel="reit" >
            <Reit id="reit" popout={this.state.popout} handleTransferChange={this.handleTransferChange} handleTransferSubmit={this.handleTransferSubmit} userAcc={this.state.userAcc} go={this.go} />
            </View>

            <View popout={this.state.popout} activePanel={this.state.activePanel}>
                <House id="house" go={this.go} />
                <Pet id="pet" go={this.go} />
                <Dop id="dop" go={this.go} />
                <Reit id="reit" clicks={this.state.clicks} reputation={this.state.reputation} go={this.go} handleReputationChange={this.handleReputationChange} handleReputationSubmit={this.handleReputationSubmit} calcRep={this.state.calcRep} />
                <ChangeNickname id="changeNickname" go={this.go} handleNickChange={this.handleNickChange} handleNickSubmit={this.handleNickSubmit} userAcc={this.state.userAcc} />
                <Transfer id="Transfer" go={this.go} handleTransferChange={this.handleTransferChange} handleTransferSubmit={this.handleTransferSubmit} userAcc={this.state.userAcc} />
                <Katalog id="katalog" go={this.go} />
			</View>
            </Epic>
		);
	}
}

export default App;
