import React from 'react';
import PropTypes from 'prop-types';
import connect from '@vkontakte/vk-connect';
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Mention from '@vkontakte/icons/dist/24/mention';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
import Icon24Replay from '@vkontakte/icons/dist/24/replay';
import Icon24Filter from '@vkontakte/icons/dist/24/filter';
import Icon32Poll from '@vkontakte/icons/dist/32/poll';
import Icon24Game from '@vkontakte/icons/dist/24/game';
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon24Live from '@vkontakte/icons/dist/24/live';
import './assets/css/bootstrap.css';
import './assets/css/style.css';
import '@vkontakte/vkui/dist/vkui.css';
import { Panel, ListItem, Button, Group, Div, Avatar, Cell, List , FixedLayout, Tabs, Alert} from '@vkontakte/vkui';

const Home = ({ id, add1, userAcc, clicks, reputation, fetchedUser, go, priv, name, dark, light }) => (
	<Panel id="home">
		
<div class="MainPage">
	<div class="MainPage__wrapper">
		<div class="MainPage__score">
			<div class="MainPage__score-header">ВАШ СЧЁТ</div>
			<div class="MainPage__score-value">
				<span >{clicks}</span>
			</div>
		</div>
		
	</div>
			</div>
			<div class="MainPage">
	<div class="MainPage__wrapper">
		<div class="MainPage__score">
			<div class="MainPage__score-header">ВАШ РЕЙТИНГ</div>
			<div class="MainPage__score-value">
				<span >{reputation}</span>
			</div>
		</div>
		
	</div>
			</div>

            
 <FixedLayout vertical="bottom">
             
      <Div>
       <Button size="xl" onClick={add1} before={<Icon24Live/>} level="destructive">Клик</Button>
     </Div>
              
            </FixedLayout>
	</Panel>
);



Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
    }),
};
export default Home;
