import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Cell, List, View, Group, Footer, Avatar, Button} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Mention from '@vkontakte/icons/dist/24/mention';
import Icon24User from '@vkontakte/icons/dist/24/user';

const osname = platform();

const Katalog = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
   Каталог игр
    </PanelHeader>   
    <Group>
    <List>
        <Cell
          photo="https://pp.userapi.com/c849420/v849420519/107095/S6gXjshe294.jpg"
          description="Игровая платежная система на платформе ВКонтакте"
          bottomContent={<Button component="a" href="https://vk.com/fustcoin">Играть</Button >}
          before={<Avatar src="https://pp.userapi.com/c849420/v849420519/107095/S6gXjshe294.jpg" size={80}/>}
          size="l"
        >
          FASTCOIN
        </Cell>
        <Cell
          photo="https://pp.userapi.com/c849128/v849128234/f6f73/kXwhC55vLvA.jpg"
          description="Игровой бот"
          bottomContent={<Button component="a" href="https://vk.com/fustcoin">Играть</Button >}
          before={<Avatar src="https://pp.userapi.com/c849128/v849128234/f6f73/kXwhC55vLvA.jpg" size={80}/>}
          size="l"
        >
          DEFUNSE
        </Cell>
         <Cell
          photo="https://pp.userapi.com/c851432/v851432558/823bf/7jFZ7UudZKg.jpg"
          description="Заходишь. Пишешь. Все"
          bottomContent={<Button component="a" href="https://vk.com/fustcoin">Играть</Button >}
          before={<Avatar src="https://pp.userapi.com/c851432/v851432558/823bf/7jFZ7UudZKg.jpg" size={80}/>}
          size="l"
        >
          ОБОИ БОТ
        </Cell>
        </List>
      </Group>
	</Panel>
);

Katalog.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Katalog;
